.board__wrapper {
  margin: 2% 0 0 0;
  display: flex;
  justify-content: center;
  width: 100%;
  
}

.board__board {
  width: 95vw;
  height: 95vw;
  display: grid;
  padding: 0px;
  border: 4px solid rgb(210 210 208);
  grid-template-columns: repeat(15, 1fr);
  grid-template-rows: repeat(15, 1fr);
  grid-gap: 1px;
  
  min-height: 350px;
  min-width: 350px;
  border-radius: 4px;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  -ms-border-radius: 4px;
  -o-border-radius: 4px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  box-shadow: inset 0 -2px 2px rgba(0,0,0,0.3),
    0px 2px 5px rgba(0, 0, 0, 0.5);
}

.board__square {
  width: 100%;
  height: 100%;
  outline: 2px solid rgba(113, 120, 128, 0.4);
  box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.3);
  font-size: 0.6rem;
  text-align: center;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  transition: outline .5s ease;
  -webkit-transition: outline .5s ease;
  -moz-transition: outline .5s ease;
  -ms-transition: outline .5s ease;
  -o-transition: outline .5s ease;
}

.board__tile {
  color: rgb(42, 102, 42);
  display: flex;
  align-items: center;
  justify-content: center;
  background: #fffcb9;
  border-radius: 2px;
  width: 100%;
  height: 100%;
  font-size: 1.1rem;
  box-shadow: 0 0 0px 1px black, inset 0 -1px 3px rgba(0, 0, 0, 0.5);
}

.board__square:hover {
  outline: 2px solid yellow;
  z-index: 3;
  transition: none;
  -webkit-transition: none;
  -moz-transition: none;
  -ms-transition: none;
  -o-transition: none;
}

.board__bonus-text {
  color: rgb(250, 244, 244);
  text-shadow:0 0 2px rgb(3, 3, 3);
  font-size: 1.2em;
}

.board__square {
  background: rgb(244, 247, 246);
}

.board__square.double-letter {
  background: rgb(190, 231, 243);
}

.board__square.triple-letter {
  background: rgb(172, 196, 252);
}

.board__square.double-word {
  background: rgb(250, 207, 168);
}

.board__square.triple-word {
  background: rgb(252, 138, 138);
}

.board__square.with-tile {
  background: purple;
}

.disabled {
  filter: grayscale(30%);
  -webkit-filter: grayscale(30%);
  opacity: 0.3;
  pointer-events: none;
}

.board__square.board__centre {
  background: rgb(251, 244, 30);
}

.center__star {
  width: 100%;
  height: auto;
  padding: 15%;
  filter: drop-shadow(0 0 1px rgb(165, 163, 163));
  -webkit-filter: drop-shadow(0 0 1px rgb(165, 163, 163));
}

