.timer__wrapper {
}

.timer__time {
    display: inline-block;
}
.warning {
    animation: pulse 1s infinite;
    -webkit-animation: pulse 1s infinite;
    color: rgb(245, 107, 16);
    text-shadow: 0 0 1px rgb(5, 5, 5);
}
  

@-webkit-keyframes pulse {
    0%, 100% {
      transform: scale(1);
      -webkit-transform: scale(1);
      -moz-transform: scale(1);
      -ms-transform: scale(1);
      -o-transform: scale(1);
    }
  
    50% {
      transform: scale(1.2);
      -webkit-transform: scale(1.2);
      -moz-transform: scale(1.2);
      -ms-transform: scale(1.2);
      -o-transform: scale(1.2);
    }
}
  

@keyframes pulse {
    0%, 100% {
      transform: scale(1);
      -webkit-transform: scale(1);
      -moz-transform: scale(1);
      -ms-transform: scale(1);
      -o-transform: scale(1);
    }
  
    50% {
      transform: scale(1.2);
      -webkit-transform: scale(1.2);
      -moz-transform: scale(1.2);
      -ms-transform: scale(1.2);
      -o-transform: scale(1.2);
    }
}
  

/* mobile */

.statusBar__frame {
  order: -1;
}

.statusBar__wrapper {
  display: flex;
  align-items: center;
  font-size: 0.8em;
  padding: 0.5em 0.5em;
  justify-content: space-around;
  flex-direction: column;
  background: rgb(240 240 240);
  border: 1px solid rgb(138 137 136);
  border-radius: 3px;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  -ms-border-radius: 3px;
  -o-border-radius: 3px;
  width: 100%;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.3),
    inset 0 -2px 2px rgba(0, 0, 0, 0.3);
  color: #222;
  transform: rotateY(0.01deg);
  transform-style: preserve-3d;
  transition: transform 0.5s ease, box-shadow 0.5s ease;
  -webkit-transition: transform 0.5s ease, box-shadow 0.5s ease;
  -moz-transition: transform 0.5s ease, box-shadow 0.5s ease;
  -ms-transition: transform 0.5s ease, box-shadow 0.5s ease;
  -o-transition: transform 0.5s ease, box-shadow 0.5s ease;
  position: relative;
  -webkit-transform: rotateY(0.01deg);
  -moz-transform: rotateY(0.01deg);
  -ms-transform: rotateY(0.01deg);
  -o-transform: rotateY(0.01deg);
}

.statusBar__reverse {
  display: flex;
  align-items: center;
  font-size: 0.7em;
  padding: 0.5em 0.5em;
  justify-content: space-around;
  content: "";
  background: rgb(240, 240, 240);
  border: 1px solid rgb(138 137 136);
  transform: rotateX(180deg);
  -webkit-transform: rotateX(180deg);
  -moz-transform: rotateX(180deg);
  -ms-transform: rotateX(180deg);
  -o-transform: rotateX(180deg);
  position: absolute;
  border-radius: 3px;
  top: 0px;
  left: -0.7px;
  width: 100.4%;
  height: 100.4%;
  box-shadow: inset 0 -2px 2px rgba(0, 0, 0, 0.3);
  backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
}

.statusBar__wrapper button {
  font-size: 1.3rem;
}

.statusBar__player {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  border-radius: 3px;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  -ms-border-radius: 3px;
  -o-border-radius: 3px;
  width: 100%;
  padding: 10px;
  height: 50%;

  /*box-shadow: inset 0 0px 8px rgba(0,0,0,0.1);
  */
}

.player__name {
  min-width: 100px;
}

.player__time {
