.chat__wrapper {
  width: 100%;
  border: 1px solid rgb(189, 189, 189);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  /*sameasstatusbarheight-toalignwithboard(mobileview)*/
  background: rgb(223, 222, 220);
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.5);
  color: #222;
  border-radius: 3px;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  -ms-border-radius: 3px;
  -o-border-radius: 3px;
  max-width: 200px;
  display: none;
}



.chat__list {
  list-style: none;
  overflow-y: auto;
  padding: 0.5rem;
  overflow-wrap: break-word;
}

.chat__wrapper form {
  display: flex;
  justify-content: center;
}

.chat__message {
  margin: 0.2rem 0;
}

.timeStamp {
  color: rgb(99, 99, 99);
  font-size: .5em;
  padding: 0 0;
}

.guest {
  color: red ;
  font-size: 8px;
  padding: 0 1em;
}

.host {
  color: blue;
  font-size: 8px;
  padding: 0 1em;
}

/* tablet */
@media (min-width: 500px) {
}

/* large tablet */
@media (min-width: 850px) {
  .chat__wrapper {
    position: relative;
    grid-column: 3;
    grid-row: 1/3;
    margin: 3% 0 3% 0;
    display: flex;
  }
  .chat__list {
    font-size: 1.5em;
  }
  .host, .guest, .timeStamp {
    font-size: .5em;
  }
}

