.gameButtons__wrapper {
  display: flex;
  justify-content: space-between;
  padding: 0.5rem 0;
}

button {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  background: rgb(140, 140, 140);
  border-radius: 3px;
  border: 1px solid #6f6f6e;
  display: inline-block;
  cursor: pointer;
  color: #ffffff;
  font-family: Arial;
  font-size: 0.8em;
  padding: 5px 7px;
  width: 100%;
  text-decoration: none;
  box-shadow: inset 0 -2px 2px rgba(0,0,0,0.3),    0px 2px 5px rgba(0, 0, 0, 0.3);
  text-shadow: 0px 0px 4px rgba(0, 0, 0, 0.6);
  white-space: nowrap;
  letter-spacing: 0.05em;
  font-family: "Acme", sans-serif;
  transform:  rotateY(0.001deg) translateY(0px);
  -webkit-transform:  rotateY(0.001deg) translateY(0px);
  -moz-transform:  rotateY(0.001deg) translateY(0px);
  -ms-transform:  rotateY(0.001deg) translateY(0px);
  -o-transform:  rotateY(0.001deg) translateY(0px);
}

.button__text {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  font-size: 1em;
  padding: 0.5em;
}

.button__exchange {
  width: 100%;
  font-size: 1em;
  padding: 0.5em;
}

button img {
  filter: drop-shadow(0px 0px 2px rgba(0, 0, 0, 0.4));
  width: 30px;
  height: auto;
  opacity: 0.9;
  margin-bottom: 0.5em;
  -webkit-filter: drop-shadow(0px 0px 2px rgba(0, 0, 0, 0.4));
}

button:hover {
  background: rgb(160, 160, 160);
}

button:active {
  position: relative;
  transform:  rotateY(0.001deg) translateY(1px);
  box-shadow: inset 0 -2px 2px rgba(0,0,0,0.3), 0px 1px 5px rgba(0, 0, 0, 0.3);
  -webkit-transform:  rotateY(0.001deg) translateY(1px);
  -moz-transform:  rotateY(0.001deg) translateY(1px);
  -ms-transform:  rotateY(0.001deg) translateY(1px);
  -o-transform:  rotateY(0.001deg) translateY(1px);
}

.gameButtons__wrapper button {
  margin: 2px;
}

@media (min-width: 450px) {
  button img {
    width: 20px;
    margin-bottom: 0;
    margin-right: 1em;
  }
  .button__text {
