/* mobile */

.gameScreen__wrapper {
  width: 100%;
  height: 100%;
  opacity: 0;
  display: flex;
  justify-content: center;
  max-width: 540px;
}

.gameScreen__main {
  width: 100%;
  display: inline-grid;
  grid-template-rows: auto 1fr auto auto;
  grid-template-columns: 1fr;
  height: 100%;
  justify-content: center;
  padding: 10px;
  min-height: 600px;
  perspective: 2000px;
  max-width: 540px;
}

.gameScreen__board {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  perspective: 1000px;
}

/* tablet */
@media (min-width: 500px) {
  
}

/* large tablet */
@media (min-width: 850px) {
   
  .gameScreen__wrapper {
    width: auto;
    height: auto;
    max-width: 100%;
  }
  
  .gameScreen__main {
    height: auto;
    grid-template-columns: minmax(150px, 200px) auto auto;
    grid-template-rows: auto 1fr auto;
    grid-gap: 10px;
    padding: 10px;
    max-width: 100%;
  }
  
  .gameScreen__board {
    display: flex;
    align-items: center;
    justify-content: space-between;
    grid-column: 2;
    grid-row: 1/3;
  }
}

